import axios, {BASEURL} from '../../utils/axios'
import {getUserId} from "@/utils/util";

/**
 * 根据日期获取血氧饱和度
 * @returns {*}
 */
export function getOxygen(userId,currentDate) {
  return axios.get(`/monitor/health/syLogOxygenController/getOxygen?userId=${userId}&currentDate=${currentDate}`);
}
/**
 * 血氧饱和度列表
 * @returns {*}
 */
export function getListByTime(userId,startTime,endTime) {
  return axios.get(`/monitor/health/syLogOxygenController/getListByTime?userId=${userId}&startTime=${startTime}&endTime=${endTime}`);
}


/**
 * 添加血氧饱和度
 * @returns {*}
 */
export function addOxygen(params) {
  return axios.post(`/monitor/health/syLogOxygenController/addOxygen`,params);
}

/**
 * 体温今日记录列表
 * @returns {*}
 */
export function getTempListByTime(userId,startTime,endTime) {
  return axios.get(`/monitor/health/syLogTemperatureController/getListByTime?userId=${userId}&startTime=${startTime}&endTime=${endTime}`);
}

/**
 * 根据日期获取体温李彪
 * @returns {*}
 */
export function getTemperature(userId,currentDate) {
  return axios.get(`/monitor/health/syLogTemperatureController/getTemperature?userId=${userId}&currentDate=${currentDate}`);
}

/**
 * 添加血氧饱和度
 * @returns {*}
 */
export function addTemperature(params) {
  return axios.post(`/monitor/health/syLogTemperatureController/addTemperature`,params);
}

/**
 * 删除血氧饱和
 * @returns {*}
 */
export function deleteOxygen(id){
  return axios.delete(`/monitor/health/syLogOxygenController/deleteOxygen?id=${id}`);
}


/**
 * 删除温度
 * @returns {*}
 */
export function deleteTemperature(id){
  return axios.delete(`/monitor/health/syLogTemperatureController/deleteTemperature?id=${id}`);
}

/**
 * 温度当日数据
 * @param startTime
 * @param endTime
 * @param userId
 * @returns {*}
 *
 */
export function temperatureHistoryList(startTime,endTime){
  return axios.get(`/monitor/health/syLogTemperatureController/getListByTime`,{ params: {
      startTime, endTime, userId: getUserId()
    }});
}


/**
 * 血氧当日数据
 * @param startTime
 * @param endTime
 * @param userId
 * @returns {*}
 *
 */
export function syLogOxygenHistoryList(startTime,endTime){
  return axios.get(`/monitor/health/syLogOxygenController/getListByTime`,{ params: {
      startTime, endTime, userId: getUserId()
    }});
}



