<template>
  <div v-show="false">{{ option.series[0].data[0] || "" }}</div>
  <v-chart class="chart" :option="option" />
</template>

<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { LineChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent
} from "echarts/components";
import VChart, { THEME_KEY } from "vue-echarts";
import {ref, defineComponent, onMounted} from "vue";

use([
  CanvasRenderer,
  LineChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent
]);

export default defineComponent({
  name: "HelloWorld",
  components: {
    VChart
  },
  props:{
    list: Array,
    type: String,
    page: String,
  },
  provide: {
    [THEME_KEY]: "White"
  },
  setup: (props) => {

    onMounted(()=>{
      const { list, type } = props
      console.log("list11111",props.page)
      for (let i = 0; i < list.length; i++) {
        option.value.xAxis.data.push(list[i].lineTime )
        option.value.series[0].data.push(list[i]['data'])
      }
      console.log( option.value.series)
    })

    const option = ref({
      title: {
        text: ''
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        data: props.page === "blod" ?  [ '血氧饱和度'] : [ '体温（摄氏度）']
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data:[]
        // data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
      },
      yAxis: {
        type: 'value',
        splitNumber : 5,
      },
      series: [
        {
          itemStyle:{
            normal:{
              color:'#00B88B', //折点颜色
              lineStyle:{
                color:'#00B88B' //折线颜色
              }
            }
          },
          name: '体温（摄氏度）',
          type: 'line',
          stack: '总量',
          data: []
        },
      ]
    });
    return { option };
  }
});
</script>

<style scoped>
.chart {
  height: 300px;
}
</style>
