<!--饮水入口-->
<template>
  <s-header :name="'历史数据'" />
  <van-tabs :background="'#F5F6FE'" :color="'#1baeae'" :title-active-color="'#1baeae'"
             v-model="status">
    <van-tab title="日">
      <HisRecord type="day" :page="type"/>
    </van-tab>
    <van-tab title="周">
      <HisRecord type="week" :page="type" />
    </van-tab>
    <van-tab title="月">
      <HisRecord type="month" :page="type" />
    </van-tab>
  </van-tabs>

</template>

<script>
import sHeader from '@/components/SimpleHeader'

import HisRecord from './blood-record'
import {reactive, onMounted, ref, toRefs } from 'vue'
import {useRouter,useRoute} from "vue-router";

export default {
  name: "index",
  components: {
    HisRecord,
    sHeader,
  },
  setup() {

    const router = useRoute()

    onMounted(
        () => {

          const { query : { type } } = router

          state.type = type
          console.log("query", state.type)
        }
    )

    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      type: '',
      todo: '',
    })

    const active = ref(2);
    // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
    return {
      ...toRefs(state),
      active,
    }
  }
}
</script>

