<template>
  <div class="scllowBox" :style="{ backgroundColor: '#F5F6FE',display: 'flex', flexDirection: 'column', paddingBottom: '10px' }">

    <div class="margin_box" :style="{  marginTop: '10px',paddingTop: '5px' }">
      <!--选择日期-->
      <change-date :style="{ backgroundColor: '#fff'}" :selectdate="selectdate" :type="type" :onchange="onchange"/>
      <!--高压低压脉搏-->
      <div class="week_genx_num">
        <p>{{ sumCount }} {{  page === 'blod' ? '%' : '℃'}}</p>
        <p>{{ page === 'blod' ? '平均血氧饱和度' : '平均日温度'}}</p>
      </div>

    </div>
    <!--本周、日、月、年数据-->
    <div class="d_h_r_item">
      <span class="span_drinkadd_title">{{ title }}</span>
      <label class="lable_item">图形分析:</label>
      <van-switch v-model="checked"  @update:model-value="onUpdateValue"  size="16px"  active-color="#15AC78" inactive-color="#dcdee0"/>
    </div>
    <!--  历史记录  -->
    <div class="margin_box scroll_box" :style="{ borderTopLeftRadius: 0, borderTopRightRadius: 0}"  >
      <div class="no_data_boold" v-show="list.length === 0">暂无数据</div>
      <HistoryRecord v-if="!checked && list.length !== 0" :list="list"  :type="type" :toYearDetial="toYearDetial"/>
      <LineECharts :page = "page" :type="type" v-else-if="checked  && list.length !== 0" :list="list"/>
    </div>
  </div>

</template>

<script>
import moment from 'moment'
import  ChangeDate from '../../../components/ChangeDate'
import  HistoryRecord from '../../component/history-record'
import   LineECharts from '../../component/line-chart'
import { onMounted, reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import { formatYYMMDD } from "@/utils/util";
import { temperatureHistoryList, syLogOxygenHistoryList } from "@/service/blood-oxygen";
export default {
  name: "momtion-record",
  components: {
    ChangeDate,
    HistoryRecord,
    LineECharts
  },
  props:{
    page: String,
    type: {
      default:'week',
      type: String,
    },
  },
  setup(props){
    const router = useRouter()

    onMounted(
        () => {
          console.log("blod",props.page)
          // 赋值
          getList()
          state.title =props.type === "day"? "本日数据": props.type === "week" ? "本周数据" :  props.type === "month" ? "本月数据"  : "本年数据"
        }
    )

    //是否是温度
    const isTemperature= () => {
      return props.page == "temperature"
    }

    // 获取数据
    const getList = async ()=>{

      const startTime =  getStartTime(props.type)
      console.log("开始时间", startTime)
      const res = props.page == "temperature" ?  await temperatureHistoryList(startTime,formatYYMMDD(state.selectdate)) :  await syLogOxygenHistoryList(startTime,formatYYMMDD(state.selectdate))
      const {code,data = [] }= res
      if(code != 200) return
      const mAfterlist = []
      const temDes  = ['低', '正常', '低热', '中热', '高热', '超高热']
      const boolds = ['正常', '偏低', '过低']
      data.map((item)=>{
         const obj = {
           ...item,
           date: moment(item.inputTime).format("YYYY-MM-DD"),
           lineTime: props.type === "day" ?  moment(item.inputTime).format("HH:mm:ss") :  moment(item.inputTime).format("MM-DD HH:mm"),
           dateTime: props.type === "day" ?  moment(item.inputTime).format("HH:mm") :  moment(item.inputTime).format("MM-DD HH:mm"),
           dataSourceDic: item.dataSource === 0 ? "手动录入" : "设备获取",
           typeDic: isTemperature() ? temDes[item.type - 1] : boolds[item.type - 1],
           data: isTemperature() ? item.temperature : item.oxygen,
           dataDic:  isTemperature() ? item.temperature + "℃" :  item.oxygen +"%"
         }
        mAfterlist.push(obj)
      })
      // 整合日数据
      // state.list = props.type === "day"? mAfterlist : handleDayDatas(mAfterlist)
      state.list = mAfterlist
      console.log("mAfterlist",state.list)
      state.sumCount = handleAverageValue(mAfterlist,props.type === "week" ? 7 : props.type === "year" ? 365 : 30 )

    }

    // 求摄入热量平均值(年、周、月)
    const handleAverageValue = (list,days) => {
      const NumList = []
      list.map((item)=>{
          NumList.push(Number(item['data']))
      })
      const sum =  NumList.reduce(function(sum,number){ //sum2 前两个数的和
        return sum + number;
      },0)
      const averageValue = sum/days || 0
      return averageValue.toFixed(2)
    }

    // 开始时间
    const getStartTime = (type) => {
      let startTime = ""
      let date = new Date(state.selectdate)
      switch (type){
        case 'day':
          startTime = state.selectdate
          break
        case 'week':
          startTime = date.setDate(date.getDate() - 7)
          break
        case 'month':
          startTime = date.setMonth(date.getMonth() - 1);
          break
        case 'year':
          startTime = date.setFullYear(date.getFullYear() - 1);
          break
      }
      return  moment(startTime).format("YYYY-MM-DD")
    }

    const handleData = (arr) => {
      if(arr.length == 0) return {
        max: 0,
        min: 0,
        average: 0
      }
      let heightMax = Math.max.apply(null,arr)
      let heightMin = Math.min.apply(null,arr);
      let heightAverage = arr.reduce((num, item, index) => {
        if(index != arr.length-1) {
          return num+item;
        } else {
          return (num+item)/arr.length;
        }
      },0);
      return {
        max: heightMax,
        min: heightMin,
        average: heightAverage.toFixed(2)
      }
    }

    // 整合日数据
    const handleDayDatas = (list) => {
      const afterData = [] // 数组
      const beforeData = list// 数组
      let tempArr = [];
      for (let i = 0; i < beforeData.length; i++) {
        if (tempArr.indexOf(beforeData[i].date) === -1) {
          afterData.push({
            date: beforeData[i].date,
            child: [beforeData[i]]
          });
          tempArr.push(beforeData[i].date);
        } else {
          for (let j = 0; j < afterData.length; j++) {
            if (afterData[j].date == beforeData[i].date) {
              afterData[j].child.push(beforeData[i]);
              break;
            }
          }
        }
      }
      console.log('年数据处理------', afterData)
      return afterData
    }

    /**
     * 跳入年详情月列表界面
     */
    const toYearDetial = (item) => {
      const json =  JSON.stringify(item.origin)
      console.log("json",json)
      router.push({ path:'/year-blood-List', query: { listJson: json, date: item.yearDate}})
    }

    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      endDate: undefined,
      lineList:[],
      todo: '',
      sumCount: 0,
      selectdate: new Date(),
      list: [],
      baseInfoList:[], // 将每日数据拆开成每次测量集合
      title: '本周数据',
      checked: false, // 是否开启图形分析
      bloodpressuredata: [{
        title:'高压',
        max:0,
        min:0,
        average:0
      },
        {title:'低压',
          max:0,
          min:0,
          average:0},
        {title:'脉搏',
          max:0,
          min:0,
          average:0}],
    })


    // 图形分析开关
    const onUpdateValue = (newValue) => {
        state.checked = newValue;
    };

    const onchange=(data) =>{
      state.selectdate = data
      state.endDate = data
      getList()
    }
    return {
      ...toRefs(state),
      onchange,
      onUpdateValue,
      toYearDetial
    }
  }
}
</script>

